"use client";

import { useState } from "react";
import { FiSearch, FiArrowLeft } from "react-icons/fi";
import { FaArrowRight } from "react-icons/fa";
import { supabase } from "../lib/supabase";

type Barang = {
  kode_barang: string;
  nama_barang: string;
  kategori: string;
  harga: number;
  stok: number;
};

export default function CariBarang({ onBack }: { onBack: () => void }) {
  const [query, setQuery] = useState("");
  const [results, setResults] = useState<Barang[]>([]);
  const [loading, setLoading] = useState(false);

  const handleSearch = async () => {
    if (!query.trim()) return;

    setLoading(true);
    const { data, error } = await supabase
      .from("barang_warput")
      .select("*")
      .ilike("nama_barang", `%${query}%`)
      .or(`kode_barang.ilike.%${query}%`);

    if (error) {
      console.error("Error saat mencari:", error.message);
    } else {
      setResults(data);
    }
    setLoading(false);
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-start bg-gray-900 text-white relative px-4 pt-32">
      <button
        onClick={onBack}
        className="absolute top-8 right-8 bg-white text-black px-4 py-2 rounded-md shadow hover:bg-gray-500 transition flex space-x-2 items-center"
      >
        <FiArrowLeft />
        <span>Kembali</span>
      </button>

      <h1 className="text-3xl md:text-4xl font-bold text-white mb-8">
        Cari Barang
      </h1>

      <div className="flex w-full max-w-xl items-center bg-white rounded-full shadow-lg overflow-hidden">
        <span className="pl-4 text-gray-400 text-xl">
          <FiSearch />
        </span>
        <input
          type="text"
          placeholder="Cari kode, nama dan model pakaian disini"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          className="flex-grow px-4 py-3 text-gray-800 bg-transparent focus:outline-none"
        />
        <button
          onClick={handleSearch}
          className="bg-black text-white px-5 py-3 rounded-r-full hover:bg-gray-800"
        >
          <FaArrowRight />
        </button>
      </div>

      {loading && <p className="mt-6 text-gray-300">Memuat...</p>}

      {results.length > 0 && (
        <div className="mt-6 w-full max-w-xl bg-white rounded-lg shadow text-black p-4">
          <h2 className="font-semibold text-lg mb-2">Hasil Pencarian:</h2>
          <ul className="divide-y divide-gray-300">
            {results.map((item) => (
              <li key={item.kode_barang} className="py-2">
                <div className="font-medium">{item.nama_barang}</div>
                <div className="text-sm text-gray-600">
                  Kode: {item.kode_barang}
                </div>
                <div className="text-sm text-gray-600">
                  Kategori: {item.kategori}
                </div>
                <div className="text-sm text-gray-600">
                  Harga: Rp {item.harga?.toLocaleString("id-ID")}
                </div>
                <div className="text-sm text-gray-600">Stok: {item.stok}</div>
              </li>
            ))}
          </ul>
        </div>
      )}

      {!loading && results.length === 0 && query.trim() !== "" && (
        <p className="mt-6 text-gray-400">Tidak ada barang ditemukan.</p>
      )}
    </div>
  );
}
